import React from 'react';
import PropTypes from 'prop-types';
import { createElementsFromText } from 'html-text-to-react';
import {
	Panel,
	PanelHeader,
	HeaderButton,
	platform,
	IOS,
	Div,
	View,
	Group,
	Form,
	Card,
	Root,
	InfoRow,
	List,CellButton,Alert,UsersStack,
	Cell,
	Link,
	PullToRefresh,
	Button,
	Component,
	PanelHeaderEdit,
	ModalPage,
	ModalPageHeader,
	FormLayoutGroup,
	FormLayout,
	ModalRoot,
	Radio,
	SelectMimicry,
	Checkbox,
	Input,
	Switch
} from '@vkontakte/vkui';
import persik from '../img/persik.png';
import './Persik.css';
import { YMaps, Map, Placemark } from 'react-yandex-maps';
import ReactDOM from 'react-dom'
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Dismiss from '@vkontakte/icons/dist/24/dismiss';
import Icon24Share from '@vkontakte/icons/dist/24/share';
import Icon24User from '@vkontakte/icons/dist/24/user';
import connect from "@vkontakte/vkui-connect";
const osname = platform();
const MODAL_PAGE_FILTERS = 'filters';
const MODAL_PAGE_COUNTRIES = 'countries';
const MODAL_PAGE_STORY_FEEDBACK = 'story-feedback';
const MODAL_PAGE_USER_INFO = 'user-info';

const MODAL_CARD_MONEY_SEND = 'money-send';
const MODAL_CARD_APP_TO_MENU = 'app-to-menu';
const MODAL_CARD_ABOUT = 'say-about';
const MODAL_CARD_NOTIFICATIONS = 'notifications';
const MODAL_CARD_CHAT_INVITE = 'chat-invite';
export default class Persik extends React.Component {

	constructor(props){
	super(props);

	this.state={
		title:"",
		text:"",
		mesto:"",
		stattime:"",
		owner_id:"",
		chatlink:"",
		grouplink:"",
		type:"",
		token:"",
		agreed_admin:"",
		can_edit:0,
		igo:0,
		bookmark:0,po:[55.751999,37.617734],metro:"",gofriends:"",gofriendsphoto:[],

		}


	}

	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}
	igo(){
		const {igo,token,eid,reload}=this.props;

		if (this.state.igo==1){
			this.setState({igo:0})
			this.get("https://api.finebot.site/events/account.noigo?token="+this.state.token+"&id="+this.state.eid)
			reload.call()

		}else{
			this.setState({igo:1})
			this.get("https://api.finebot.site/events/account.igo?token="+this.state.token+"&id="+this.state.eid)
			reload.call()

		}
	}
	editbookmark(){
		if (this.state.bookmark==1){
			this.setState({bookmark:0})
		}else{
			this.setState({bookmark:1})

		}
	}
	componentWillMount() {
		const {popout,errorOpen}=this.props;
		const {token}=this.props;
		const {eid,startloading,stoploading}=this.props;


		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppCallAPIMethodResult':

					setTimeout(()=>{
						var t = this.post("https://api.finebot.site/events/events.getById?token="+token+"&eid="+eid,JSON.stringify(e.detail.data))

						if(t.startsWith("{\"error\":{")){
							errorOpen.call()
						}else{
							alert(t)
							t=JSON.parse(t)
							var text=t.response[0].text



							var br = React.createElement("br")
							var f =text.split("\n").join("<br/>")
							this.setState({text:f,title:t.response[0].title,mesto:t.response[0].country+", "+t.response[0].city+", "+t.response[0].street,starttime:t.response[0].starttime,chatlink:t.response[0].chatlink,grouplink:t.response[0].grouplink,
								agreed_admin:t.response[0].agreed_admin,can_edit:t.response[0].can_edit,igo:t.response[0].igo,type:t.response[0].type,owner_id:t.response[0].owner_id,token:token,eid:eid,metro:t.response[0].metro})

							if(t.response[0].friends==null){

							}else{
								this.setState({gofriends:t.response[0].friends.text,gofriendsphoto:t.response[0].friends.photo})
							}

							var api = this.get1(`https://geocode-maps.yandex.ru/1.x/?format=json&geocode=`+encodeURIComponent(t.response[0].country+", "+t.response[0].city+", "+t.response[0].street))
							api=JSON.parse(api)
							var po=[]
							po[0]=api.response.GeoObjectCollection.featureMember[0].GeoObject.Point.pos.split(" ")[1]
							po[1]=api.response.GeoObjectCollection.featureMember[0].GeoObject.Point.pos.split(" ")[0]
							this.setState({po:po})
							stoploading.call()

						}

					})
					break;
				default:

			}
		});

		connect.send("VKWebAppCallAPIMethod", {"method": "friends.get", "request_id": "friends", "params": {"fields":"photo","count":10000,"v":"5.101", "access_token":token}});

		connect.send("VKWebAppSetLocation", {"location": "e"+eid});

	}
	post(url,array){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.setRequestHeader('Content-Type', 'application/x-www-form-urlencoded');
		xhr.send(array);
		return xhr.responseText;
	}
	componentDidMount() {





	}



	buttons(){
		const {igo,token,eid,reload}=this.props;

			if(this.state.igo==1){
				return(
					<Div style={{display: 'flex'}}>
						<Button size="l" stretched style={{ marginRight: 8 }} level="outline" onClick={()=>{this.igo()}}>Не пойду</Button>
						{this.getEditButton()}


					</Div>
				);
			}else if(this.state.igo==0){
				return(
					<Div style={{display: 'flex'}}>
						<Button size="l" stretched style={{ marginRight: 8 }} onClick={()=>{this.igo()}}>Я пойду</Button>
						{this.getEditButton()}

					</Div>

				);
			}

	}
	get1(url){
		var xhr = new XMLHttpRequest();
		xhr.open('GET', url, false);
		xhr.send();
		return xhr.responseText;
	}
	getFriends(){
		if(this.state.gofriends===""){

		}else{
			return(
				<Group>
					<UsersStack
						photos={this.state.gofriendsphoto}
					>{this.state.gofriends}</UsersStack>
				</Group>
			);
		}
	}
	getOwner(){
		const {title,text,go,mesto,starttime,type,owner_id}=this.props;




		var own="https://vk.com/id"+this.state.owner_id
		if(this.state.owner_id==="undefined"||this.state.owner_id===""||this.state.owner_id==null){
			return ""
		}else{
			return (<Cell>
				<InfoRow title="Организатор">
					<Link href={own}>vk.com/id{this.state.owner_id}</Link>
				</InfoRow>
			</Cell>);
		}
	}
	getChatlink(){
		const {title,text,go,mesto,starttime,type,owner_id,chatlink}=this.props;


		if(this.state.chatlink==="undefined"|| this.state.chatlink==="null" || this.state.chatlink===""){
			return ""
		}else{
			return (<Cell>
				<InfoRow title="Ссылка на чат">
					<Link href={this.state.chatlink}>{this.state.chatlink}</Link>
				</InfoRow>
			</Cell>);
		}
	}
	getGroup(){
		const {title,text,go,mesto,starttime,type,owner_id,grouplink}=this.props;


		if(this.state.grouplink==="undefined"|| this.state.grouplink==="null" || this.state.grouplink===""){
			return ""
		}else{
			return (<Cell>
				<InfoRow title="Группа">
					<Link href={this.state.grouplink}>{this.state.grouplink}</Link>
				</InfoRow>
			</Cell>);
		}
	}
	getMetro(){
		const {title,text,go,mesto,starttime,type,owner_id,grouplink}=this.props;


		if(this.state.metro==="undefined"|| this.state.metro==="null" || this.state.metro===""){
			return ""
		}else{
			return (<Cell>
				<InfoRow title="Ближайшее метро">
					{this.state.metro}
				</InfoRow>
			</Cell>);
		}
	}

	agreed_admin(){
		const {title,text,go,mesto,starttime,type,owner_id,agreed_admin}=this.props;


		if(this.state.agreed_admin==="undefined"|| this.state.agreed_admin==="null"){
			return (
				<Cell>
					<InfoRow title="Согласованность с администрацией города">
						Не требуется
					</InfoRow>
				</Cell>
			);
		}else{
			return (<Cell>
				<InfoRow title="Согласованность с администрацией города">
					{this.state.agreed_admin === "1"? "Согласовано" : "Не согласовано"}
				</InfoRow>
			</Cell>);
		}
	}

	getEditButton(){
		const {title,text,go,mesto,starttime,type,owner_id,can_edit}=this.props;
		if(this.state.can_edit==1){
			return(

					<Button size="l" stretched style={{ marginRight: 8 }} level="tertiary" data-to="edit" onClick={go}>Редактировать</Button>


			);
		}else{
			return "";
		}
	}


	render() {

		const {title,text,go,mesto,starttime,type,owner_id,can_edit}=this.props;




		return (




					<Panel id="full">

						<PanelHeader
							left={<HeaderButton onClick={go} data-to="home">{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}>
							Событие
						</PanelHeader>
						<Group><Div>{this.state.title}</Div></Group>
						<Group>
							<YMaps>
								<Map width="100vw" height="25vh"
									 defaultState={{ center: this.state.po, zoom: 14 }}>
									<Placemark geometry={this.state.po} />
								</Map>
							</YMaps>
						</Group>
						{this.buttons()}
						{this.getFriends()}
						<Group>
							<Div>
								<InfoRow title="Примечание" id="prim">
									{createElementsFromText(this.state.text, {whitelistedHtmlTags: ['br'],})}
								</InfoRow>

							</Div>
						</Group>


						<Group>
							<List>

								<Cell>
									<InfoRow title="Место">
										{this.state.mesto}
									</InfoRow>
								</Cell>
								{this.getMetro()}
								<Cell>
									<InfoRow title="Начало">
										{this.state.starttime}
									</InfoRow>
								</Cell>
								<Cell>
									<InfoRow title="Тип">
										{this.state.type}
									</InfoRow>
								</Cell>


								{this.getOwner()}
								{this.getChatlink()}
								{this.getGroup()}
								{this.agreed_admin()}

							</List>

						</Group>
					<Group>
						<CellButton before={<Icon24Share />} onClick={()=>connect.send("VKWebAppShare", {"link": "https://vk.com/app7077031#e"+this.state.eid})}>Поделиться</CellButton>
					</Group>
					</Panel>




		);
	}
}
const style={
	fd: {

		width: '100%',
		display: 'flex',
		height: '100%',
		justifyContent: 'bottom',


	},
	buttons:{
		height: '100%',
		padding: '3px',
		width: '100%',


	},
};
Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


