import React from 'react';
import PropTypes from 'prop-types';
import {
	Panel,
	PanelHeader,
	HeaderButton,
	platform,
	IOS,
	Div,
	View,
	Group,
	Radio,
	Form,
	Card,
	Root,
	InfoRow,
	ModalRoot,
	List,
	ModalCard,
	Cell,
	Link,
	PullToRefresh,
	Button,
	CellButton,
	SelectMimicry,
	FormLayout,
	Input,
	Textarea,
	ScreenSpinner, Switch, Alert
} from '@vkontakte/vkui';
import persik from '../img/persik.png';
import './Persik.css';
import ReactDOM from 'react-dom'
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Done from '@vkontakte/icons/dist/24/done';
import Icon24Add from '@vkontakte/icons/dist/24/add';
import Full from "./Full";
import Edit from "./Edit";
import Create from "./Create";



const osname = platform();

export default class Persik extends React.Component {

	constructor(props){
	super(props);

	this.state={
		title:"",
		text:"",
		events:[],
		sevents:[],
		mesto:"",
		stattime:"",
		owner_id:"",
		activePanel: 'my', igos:[], pustigos:"",notify:"0",allownotify:"0",sw:"",
		chatlink:"",
		type:"",
		token:"fd",
		grouplink:"",
		edt:"",
		eid:0,
		unixtimestop:"",igo:0,
		unixtime:"",
		agreed_admin:"",
		your_city:"",
		can_edit:0,
		city:[],
		popout:<ScreenSpinner />,
		test:null,
		sogl:1,
		stype:[],
		types:[],
		verdict:0,
		activeModal:null,
		}


	}
	saveedit(){

		this.setState({ activePanel: "my",

			title:"",
			text:"",
			events:[],
			sevents:[],
			mesto:"",
			stattime:"",
			owner_id:"",
			activeStory: 'feed', igos:[], pustigos:"",
			chatlink:"",
			type:"",

			grouplink:"",
			edt:"",
			eid:0,
			unixtimestop:"",igo:0,
			unixtime:"",
			agreed_admin:"",
			your_city:"",
			can_edit:0,
			city:[],
			popout:<ScreenSpinner />,
			test:null,
			sogl:1,
			stype:[],
			types:[],})

		setTimeout(this.load.bind(this))
		setTimeout(this.notsave.bind(this))



	};
	componentDidMount() {
		const {token}=this.props;
		this.setState({token:token})
		setTimeout(this.startloading.bind(this))
		setTimeout(this.load.bind(this))

	}
	closePopout(){
		this.setState({popout:""})
	}
	notsave()  {
		this.setState({ popout:
				<Alert
					actions={[{
						title: 'ОК',
						autoclose: true,


					}]}
					onClose={this.closePopout.bind(this)}
				>
					<h2>Готово</h2>
					<p>Изменения сохранены.</p>
				</Alert>
		});
	}
	load(){
		setTimeout(()=>{
			console.log("ff")
			if(this.state.events.length==0){
				console.log("start")

				var exp=""
				var re=this.get("https://api.finebot.site/events/events.my?token="+this.state.token)
				re=JSON.parse(re)
				var i =0
				for(i;i<re.response.length;i++){
					var mesto=re.response[i].country+", "+re.response[i].city+", "+re.response[i].street

					this.state.events.push(encodeURIComponent(re.response[i].title)+"&&&"+encodeURIComponent(mesto)+"&&&"+encodeURIComponent(re.response[i].starttime)+"&&&"+encodeURIComponent(re.response[i].id)+"&&&"+encodeURIComponent(re.response[i].verdict))

				}

				this.setState({your_city:re.your_city,city:re.city,types:re.types,allownotify:re.allownotify})




			}
			this.setState({fetching:false})
			this.setState({popout:null})



			if(this.state.events.length==0){
				this.setState({test:<Div style={{ paddingTop: 60, paddingBottom: 60, color: 'gray', width:'100%',textAlign:'center',fontSize:24}}>Вы еще не создали ни одного события.</Div>})

			}else{
				this.setState({test:null})

			}
			this.stoploading.bind(this)
		})
	}
	startloading(){
		this.setState({popout:<ScreenSpinner />})
	}
	stoploading(){
		this.setState({popout:null})

	}
	goo(i){

		setTimeout(this.setState({eid:this.state.events[i].split("&&&")[3],activePanel:"full"}))

	}
	ret(item, i){

		var title=(decodeURIComponent(item.split("&&&")[0])).split("+").join(" ")
		var mesto=decodeURIComponent(item.split("&&&")[1])
		var datetime=decodeURIComponent(item.split("&&&")[2])
		var eid=decodeURIComponent(item.split("&&&")[3])
		var verdict=decodeURIComponent(item.split("&&&")[4])



		switch (verdict) {
			case "0":
				verdict=<form>На рассмотрении</form>
				break;
			case "1":
				verdict=<form style={{color:'green'}}>Опубликовано</form>
				break;
			case "2":
				verdict=<form style={{color:'red'}}>Отклонено</form>
				break;
		}

		const {go}=this.props
		return(

			<Group >
				<Div>
					<form style={{textAlign:'down',width:'100%',marginBottom:5}} >{title}
					</form>
					<form style={{fontSize:12, textAlign:'top',marginBottom:15}}>{decodeURIComponent(mesto).split("+").join(" ")}<br/>{datetime}<br/>{verdict}
					</form>


					<Button onClick={()=>this.goo(i)} data-to="full">Открыть</Button>
				</Div>


			</Group>

		);
	}
	gotovo(){
		this.setState({ activePanel: "my",

			title:"",
			text:"",
			events:[],
			sevents:[],
			mesto:"",
			stattime:"",
			owner_id:"",
			activeStory: 'feed', igos:[], pustigos:"",
			chatlink:"",
			type:"",

			grouplink:"",
			edt:"",
			eid:0,
			unixtimestop:"",igo:0,
			unixtime:"",
			agreed_admin:"",
			your_city:"",
			can_edit:0,
			city:[],
			popout:<ScreenSpinner />,
			test:null,
			sogl:1,
			stype:[],
			types:[],})

		setTimeout(this.load.bind(this))



		this.setState({activeModal:"modal-send",activePanel:"my"})
	}
	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}

	go = (e) => {
		if(e.currentTarget.dataset.to==="home"){
			e.currentTarget.dataset.to="my"
		}
		this.setState({ activePanel: e.currentTarget.dataset.to })

	};

	reload(){
		console.log("fff")
		setTimeout(this.setState({
			events:[],
			igos:[],
			city:[],
			popout:<ScreenSpinner />}))

		setTimeout(this.load.bind(this))
	}
	render() {

		const {setView}=this.props;
		const modal= (<ModalRoot activeModal={this.state.activeModal}>
			<ModalCard
				id="modal-send"
				onClose={() => null}
				icon={<Icon24Done />}
				title="Ваше событие отправлено на проверку."
				caption="Мы сообщим Вам о любых изменениях, если Вы включили оповещения."
				actions={[
					{
						title: 'Готово',
						type: 'primary',
						action: () => {
							this.setState({activeModal:null})
						}
					}
				]}
			>

			</ModalCard>

		</ModalRoot>);
		return (


			<View id="my" activePanel={this.state.activePanel} popout={this.state.popout} modal={modal}>
				<Panel id="my">
					<PanelHeader
						left={<HeaderButton onClick={setView} data-to="glav">{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}>
						Мои мероприятия
					</PanelHeader>
					<Div><Button level="tertiary" onClick={this.go} data-to="create" stretched before={<Icon24Add />}>Добавить событие</Button></Div>
					<div style={{marginBottom:60}}>{this.state.events.map(this.ret.bind(this))}</div>
					<Div style={{display: 'flex'}}>
						{this.state.test}
					</Div>

				</Panel>
				<Full  startloading={this.startloading.bind(this)} stoploading={this.stoploading.bind(this)} id="full" reload={()=>this.reload()} eid={this.state.eid} igo={this.state.igo} go={this.go} title={this.state.title} text={this.state.text} mesto={this.state.mesto} starttime={this.state.starttime} agreed_admin={this.state.agreed_admin} grouplink={this.state.grouplink} type={this.state.type} owner_id={this.state.owner_id} chatlink={this.state.chatlink} can_edit={this.state.can_edit} token={this.state.token}/>
				<Edit  startloading={this.startloading.bind(this)} stoploading={this.stoploading.bind(this)} id="edit" saveedit={this.saveedit.bind(this)} token={this.state.token} eid={this.state.eid} unixtimestop={this.state.unixtimestop} city={this.state.city} unixtime={this.state.unixtime} go={this.go} title={this.state.title} text={this.state.text} mesto={this.state.mesto} starttime={this.state.starttime} agreed_admin={this.state.agreed_admin} grouplink={this.state.grouplink} type={this.state.type} types={this.state.types} owner_id={this.state.owner_id} chatlink={this.state.chatlink} can_edit={this.state.can_edit} your_city={this.state.your_city}/>
				<Create id="create" gotovo={this.gotovo.bind(this)} token={this.state.token} eid={this.state.eid} unixtimestop={this.state.unixtimestop} city={this.state.city} unixtime={this.state.unixtime} go={this.go} title={this.state.title} text={this.state.text} mesto={this.state.mesto} starttime={this.state.starttime} agreed_admin={this.state.agreed_admin} grouplink={this.state.grouplink} type={this.state.type} types={this.state.types} owner_id={this.state.owner_id} chatlink={this.state.chatlink} can_edit={this.state.can_edit} your_city={this.state.your_city}/>

			</View>

		);
	}
}
const style={
	fd: {

		width: '100%',
		display: 'flex',
		height: '100%',
		justifyContent: 'bottom',


	},
	buttons:{
		height: '100%',
		padding: '3px',
		width: '100%',


	},
};
Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


